import { getCss, buildResponsiveStyles, mergeDeep } from '../../utils';
import {
  TFlexItemAlignSelf,
  FlexItemAlignSelfType,
  TFlexItemFlex,
  FlexItemFlexType,
  TFlexGrow,
  FlexGrowType,
  TFlexShrink,
  FlexShrinkType,
  TFlexItemOffset,
  FlexItemOffsetType,
  TFlexItemWidth,
  FlexItemWidthType,
} from './flex-item-utils';

const flexItemWidths: { [key in Exclude<FlexItemWidthType, 'auto'>]: number } & {
  none: number;
  auto: string;
} = {
  none: 0,
  'one-quarter': 25,
  'one-third': 33.333333,
  half: 50,
  'two-thirds': 66.666667,
  'three-quarters': 75,
  full: 100,
  auto: 'auto',
};

export const createStyles = (
  alignSelf: TFlexItemAlignSelf,
  flex: TFlexItemFlex,
  grow: TFlexGrow,
  shrink: TFlexShrink,
  offset: TFlexItemOffset,
  width: TFlexItemWidth
): string => {
  return getCss({
    '@global': {
      ':host': {
        minWidth: 0,
        boxSizing: 'border-box',
        ...mergeDeep(
          buildResponsiveStyles(width, (widthResponsive: FlexItemWidthType) => ({
            width: `${flexItemWidths[widthResponsive]}%`,
          })),
          buildResponsiveStyles(alignSelf, (alignSelfResponsive: FlexItemAlignSelfType) => ({
            alignSelf: alignSelfResponsive,
          })),
          buildResponsiveStyles(offset, (offsetResponsive: FlexItemOffsetType) => ({
            marginLeft: `${flexItemWidths[offsetResponsive]}%`,
          })),
          flex !== 'initial' // flex shorthand conflicts with grow and shrink, which means even default grow or shrink props would override flex
            ? buildResponsiveStyles(flex, (flexResponsive: FlexItemFlexType) => ({
                flex: flexResponsive === 'equal' ? '1 1 0' : flexResponsive,
              }))
            : mergeDeep(
                buildResponsiveStyles(grow, (flexGrow: FlexGrowType) => ({ flexGrow })),
                buildResponsiveStyles(shrink, (flexShrink: FlexShrinkType) => ({ flexShrink }))
              )
        ),
      },
    },
  });
};
